import axios from 'axios'
// const baseUrl = 'api/couples'
const baseUrl = 'http://localhost:3001/api/couples'

const getAll = () => {
  const request = axios.get(baseUrl)
    console.log("getall accionado desde frontend")
  return request.then(response => {
    return response.data
  })
}

const getMovies = () => {
  const request = axios.get(baseUrl + '/movies')
  return request.then(response => {
    return response.data
  })
}

const getTvShows = () => {
  const request = axios.get(baseUrl + '/tv-shows')
  return request.then(response => {
    return response.data
  })
}

const create = newObject => {
  console.log("post accionado desde frontend")
  return axios.post(baseUrl, newObject)
}

const update = (id, newObject) => {
  console.log("update accionado desde frontend")
  const request = axios.put(`${baseUrl}/${id}`, newObject)
  return request.then(response => response.data)
}

export default {
  getAll,
  getMovies,
  getTvShows,
  create,
  update
}
