import React, {useEffect, useState} from 'react';
import Header from './Header.js';
import Nav from './Nav.js';
import Aside from './Aside.js';
import Footer from './Footer.js';
import DetailedCard from './DetailedCard.js';
import AddForm from './AddForm.js';
import Section from './Section'
import coupleService from './services/couples';
import {
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom"

const App = () => {
  // mis States (o hooks?)
  const [popUp, setPopUp] = useState(false);
  const [isAddFormOn, setIsAddFormOn] = useState(false);
  const [display, setDisplay] = useState(0);
  const [activeView, setActiveView] = useState(0)
  const [tvCouples, setTvCouples] = useState([]);
  const [movieCouples, setMovieCouples] = useState([]);
  const [globalCouplesArray, setGlobalCouplesArray] = useState([]);
  const [couples, setCouples] = useState([])

  // load couples
  useEffect(() => {
    coupleService
      .getAll()
      .then(response => {
        setCouples(response)
        setGlobalCouplesArray(response);
      })
      .catch(error => {
        console.log('fail in retrieving couples')
        console.log(error)
      })
      coupleService
        .getMovies()
        .then(response => {
          setMovieCouples(response);
        })
        .catch(error => {
          console.log('fail in retrieving movie couples')
          console.log(error)
        })
      coupleService
        .getTvShows()
        .then(response => {
          setTvCouples(response);
        })
        .catch(error => {
          console.log('fail in retrieving tv couples')
          console.log(error)
        })
  }, [])

  // handle clicks
    // handle clicks for modal
    const handleClick = (newId) => {
      // esto dice que hay que mostrar la detailed card
      setPopUp(popUp => !popUp);
      // esto le comunica a detailed card quÃ© card mostrar
      setDisplay(newId);
    }

    //handle clicks for add form
    const handleFormClick = () => {
      setIsAddFormOn(isAddFormOn => !isAddFormOn)
      console.log("handleFormClick activado desde app.js")
    }

  // handle click for view change (cancelado)

  //toggle watched in modal/detaled card
  const toggleWatchedOf = id => {
    // encuentra la couple al que hay que toggle el watch
    // const url = `http://localhost:3001/couples/${id}`
    const couple = couples.find(c => c.id === id)
    console.log("toggleWatchedOf activado desde App.js")

    const changedCouple = { ...couple, watchedByUser: !couple.watchedByUser }

    // cambia la couple en el servidor, luego cambia la variable (couples) con el que estÃ¡ funcionando la pÃ¡gina
    coupleService
      .update(id, changedCouple)
      // .then(response => {
      //   setCouples(couples.map(couple => couple.id !== id ? couple : response.data))
      // })
      .catch(error => {
        console.log('fail in updating couple')
      })
  }

  // updates state of couples after new couple has been posted to server
  const updateSetCouples = (newThingy) => {
    const newCouple = newThingy;
    setCouples(couples.concat(newCouple));
  }


  const updateView = (nbr) => {
    setActiveView(nbr);
    if (nbr === 2) {
      setCouples(movieCouples)
    } else if (nbr === 1) {
      setCouples(tvCouples)
    } else if (nbr === 0) {
      setCouples(globalCouplesArray)
    }
  }

  return (
    <div>
      <BrowserRouter>
      <div className="AppLanding">
        {popUp && <DetailedCard handleClick={handleClick} displayId={display} couples={couples} toggleWatched={toggleWatchedOf}/>}
        <Header />
        <Nav updateView={updateView} activeView={activeView} handleFormClick={handleFormClick}/>
        <Aside />
        {isAddFormOn && <AddForm couples={couples} handleFormClick={handleFormClick} updateSetCouples={updateSetCouples}/>}

        <Switch>
          <Route path="/movies">
          <div className="section-wrapper">
            <Section className="Section" handleClick={handleClick} couples={movieCouples} activeView={activeView} />
          </div>
          </Route>
          <Route path="/tv-shows">
          <div className="section-wrapper">
            <Section className="Section" handleClick={handleClick} couples={tvCouples} activeView={activeView} />
          </div>
          </Route>
          <Route path="/">
          <div className="section-wrapper">
            <Section className="Section" handleClick={handleClick} couples={couples} activeView={activeView}  />
          </div>
          </Route>
        </Switch>

        <Footer />
      </div>
      </BrowserRouter>
    </div>
  )
}

export default App;
