import React from 'react';
import {
  BrowserRouter as Router,
  Link,
} from "react-router-dom"

function Nav(props) {

  const activeView = props.activeView;
  var movieClass = "";
  var tvClass = "";
  var globalClass = "";
  if (activeView === 2) {
    movieClass = "Active"
  } else if (activeView === 1) {
    tvClass = "Active"
  } else if (activeView === 0) {
    globalClass = "Active"
  }

  return (
    <nav className="Nav">
    {/* forceRefresh={true} */}
    <Router>
      <ul>
        <li className={globalClass} onClick={() => {props.updateView(0)}}>
            <Link to="/"><span id="globalNavWord"> Global </span> </Link>
        </li>
        <li className={tvClass} onClick={() => { props.updateView(1)}}>
          <Link to="/tv-shows"><span id="globalNavTv">TV Shows</span></Link>
        </li>
        <li className={movieClass} onClick={() => { props.updateView(2)}}>
          <Link to="/movies"><span id="globalNavMovies">Movies</span></Link>
        </li>
        <li id="displayAddOn" onClick={props.handleFormClick}>
          Add
        </li>
      </ul>
      </Router>
    </nav>
  );
}

export default Nav;
